import { useEffect, useState } from "react";
import styles from "./styles/app.module.css";

import { data } from "../src/data/kaijuList";
import Question from "./components/Question";
import AnswerPicker from "./components/AnswerPicker";

export default function App() {
  const [question, setQuestion] = useState();
  const [trackerList, setTrackerList] = useState(
    data.map((currElement, index) => {
      return index;
    })
  );
  const [answer, setAnswer] = useState();

  const [score, setScore] = useState(0);

  function handleTurn() {
    let randomIndex = Math.floor(Math.random() * trackerList?.length);
    let questionIndex = trackerList[randomIndex];
    setQuestion(data[questionIndex]);
    setTrackerList((currentList) =>
      currentList.filter((elem, i) => i !== randomIndex)
    );
    // allows rerender when answer index doesn't change
    setAnswer(null);
  }

  function handleSelect(answerIndex) {
    setAnswer(answerIndex);
  }

  useEffect(() => {
    if (question && answer !== null) {
      if (data[answer]?.name === question.name) {
        console.log("correct");
        setScore(score + 1);
      } else {
        console.log("wrong");
      }
      handleTurn();
    }
  }, [answer]);

  function reset() {
    setTrackerList(
      data.map((currElement, index) => {
        return index;
      })
    );
    setScore(0);
  }

  return (
    <div className={styles.mainContainer}>
      {question && <Question text={question?.name} />}
      {question && <AnswerPicker handleSelect={handleSelect} />}
      {!question && trackerList.length > 0 && (
        <button type="button" onClick={handleTurn}>
          Play
        </button>
      )}
      {!question && trackerList.length === 0 && (
        <>
          <h2>Score: {score}</h2>
          <button type="button" onClick={reset}>
            Home
          </button>
        </>
      )}
    </div>
  );
}
